.modal-background {
    background-color: rgb(253, 224, 135);
    width: 200vw;
    height: 100vh;
    position: fixed;
}

.modal {
    width: 800px;
    height: 400px;
    background-color: white;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border: 2px solid black;
    border-radius: 6px;
}

.modal-title {
    font-size: 4rem;
}

.modal-text {
    font-size: 2rem;
}

.x-close {
    display: flex;
    margin-left: 773px;
    margin-top: 5px;
    padding: 2px 3px;
    font-size: large;
    font-weight: 900;
}

.btn-close {
    padding: 8px 25px;
    margin-top: 50px;
    font-size: large;
    border-radius: 5px;
    font-weight: 900;
}